  methods: {
    getProducts() {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products/`;
      this.loader = true;
      this.$http.get(api)
      .then((res) => {
        const data = res.data.products;
        data.sort(() => Math.random() - 0.5);
        this.randomProducts = data.slice(0, 4);
        this.loader = false;
      })
    },
  },
  created() {
    this.getProducts();
